// === MESSAGE COMPONENT ===
.messages h2 {
  font-family: "Reenie Beanie", cursive;
  font-size: 10rem;
  font-weight: 400;
  padding: 25px 0;
  position: sticky;
  text-align: center;
  top: 0;
}

.message {
  // background: lighten($white, 10);
  // box-shadow: inset 0 0 100px lighten($white, 5);
  border: 3px solid $white;
  border-radius: 7px;
  color: lighten($white, 5);
  margin: 20px;
  max-width: 75%;
  overflow-wrap: break-word;
  padding: 30px;
  float: left;
  p {
    letter-spacing: 0.4rem;
    &.annie {
      font-family: "Annie Use Your Telescope", cursive;
    }
    &.gaegu {
      font-size: 5rem;
      font-family: "Sacramento", cursive;
    }
    &.reenie {
      font-family: "Reenie Beanie", cursive;
      font-size: 4rem;
    }
    &.sueEllen {
      font-family: "Sue Ellen Francisco", cursive;
    }
    &.swanky {
      font-family: "Swanky and Moo Moo", cursive;
      text-transform: uppercase;
    }
    &.arial {
      font-family: Arial, Helvetica, sans-serif;
      font-size: 1.6rem;
    }
  }
  &.white {
    border-color: $white;
  }
  &.green {
    border-color: $success;
  }
  &.blue {
    border-color: $primary;
  }
  &.purple {
    border-color: $accent;
  }
  &.yellow {
    border-color: $secondary;
  }
  &.red {
    border-color: $error;
  }
  &.whiteBkg {
    background: $white;
    color: $black;
  }
}
